.cats{display:grid;grid-template-columns:repeat(auto-fill, minmax(160px, 1fr));gap:32px;row-gap:32px;justify-content:space-between;margin-bottom:72px}.blockTitle{margin-bottom:40px}.item{width:160px;margin:0 auto;display:none}.item:nth-child(-n+7){display:block}.item_active{display:block}.imageWrapper{width:160px;height:160px;background-color:#e8ecf1;border-radius:100%;display:flex;align-items:center;justify-content:center;margin-bottom:26px}.title{font-size:17px;text-align:center}.button{display:flex;justify-content:center}@media(max-width: 1391.9px){.item:nth-child(7){display:none}}@media(max-width: 1199.9px){.item:nth-child(6){display:none}}